export default {
	data() {
		return {
			imgUrl: '/static/bule.jpg',
			num: 2,
			index: 1,
			isNum: 3,
			dataText: ''
		}
	},
	methods: {
		changeMethoed (val) {
			this.num = val
		},
		back () {
			uni.navigateBack({
				delta:1
			})
		},
		close (val) {
			this.index = val
		},
		open (value) {
			this.index = value
		},
		changeValue (value) {
			if (value == 0) {
				this.dataText = '网络正常'
			} else if (value == 1) {
				this.dataText = '暂无更新'
			} else if (value == 2) {
				this.dataText = '上传日志'
			} else if (value == 3) {
				this.dataText = '请登录'
			} else if (value == 4) {
				this.dataText == '请登录'
			} else {
				this.dataText = '请登录'
			}
		}
	}
}
